.WebSite{
    width: 100%;
    height: 100%;
    display: flex;
    padding: 3%;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}
    .webSiteImage{
        background-color: #61dafb;
        height: 100%;
        margin-right: 5%;
        width: 50%;
    }
    .webSiteContent{
        height: 100%;
        width: 45%;
    }
        .webSiteName{
            height: 15%;
            background: -webkit-linear-gradient( black, black);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: 4vh;
        }
        .webSiteDesc{
            height: 60%;
            display: flex;
            flex-direction: column;
        }
        .webSiteTechnologies{
            height: 10%;
        }
        .webSiteOperations{
            height: 10%;

        }
            .webSiteOpenBTN{
                float: right;
                width: 25%;
                height: 100%;
                background-color: var(--primary);
                border-radius: 3vh;
                display: flex;
                align-items: center;
                justify-content: center;
            }


@media (pointer:none), (pointer:coarse) {
    .WebSite{
        animation: ;
        width: 100%;
        height: 100%;
        display: flex;
        padding: 0%;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
    }
    .webSiteImage{
        height: 60%;
        margin-right: 0%;
        width: 100%;
    }
    .webSiteContent{
        height: 40%;
        width: 100%;
    }
    .webSiteName{
        height: 15%;
        background: -webkit-linear-gradient( black, black);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 4vh;
    }
    .webSiteDesc{
        height: 50%;
        display: flex;
        flex-direction: column;
    }
    .webSiteTechnologies{
        width: 100%;
        height: 20%;
        font-size: 2vh;
    }
    .webSiteOperations{
        height: 15%;
        width: 100%;
    }
    .webSiteOpenBTN{
        float: right;
        width: 30%;
        height: 100%;
        background-color: var(--primary);
        border-radius: 3vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }


}