.Portfolio{
    padding: 5%;
    height: 100%;
    background: -webkit-linear-gradient(silver, antiquewhite);
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    overflow: auto;
}

.categoriesHolder{
    height: 90%;
    width: 100%;
    overflow: hidden;

}
    .categoryHolder{
        width: 100%;
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        overflow: hidden;
    }
    .moveLeft{
        height: 100%;
        width: 5%;
        display: flex;
        align-items: center;
        justify-content: center;
    }


    .ProductsContainer{
        height: 100%;
        width: 90%;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        overflow: hidden;
    }

    .moveRight{
        height: 100%;
        width: 5%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
        .nextImage{
            width: 100%;
        }

.tabsMenu{
    height: 10%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-top: 4px double var(--primary);
    border-bottom: 4px double var(--primary);
}

.tab{
    width: 20%;
}

a.tabEntry{
    color: black;

    width: 25%;
}

@media (pointer:none), (pointer:coarse) {

    .Portfolio{
    }


    .categoriesHolder{

    }
    .categoryHolder{

    }
    .moveLeft{
        width: 0%;
    }


    .ProductsContainer{

        overflow: scroll;
    }

    .moveRight{
        width: 0%;
    }
    .nextImage{
        width: 100%;
    }

    .tabsMenu{
        height: 10%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-around;
        border-top: 4px double var(--primary);
        border-bottom: 4px double var(--primary);
    }

    .tab{
        width: 15%;
    }

    a.tabEntry{
        color: black;
    }
}