.Home{
    width: 100%;
    background: -webkit-linear-gradient(rgb(192,192,192, 0.8), rgb(0, 0, 0, 0.6), rgb(0, 0, 0, 0.8), rgb(0, 0, 0, 0.9));
    height: 100%;
    padding-top: 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-items: center;
    justify-content: center;
}

.homeLogo{
    height: 30%;
    align-self: center;
    justify-self: center;
    color: #61dafb;
    font-size: 8vh;
    font-family: 'Merienda', cursive;
}

    .homeText{
        color: white;
        font-size: 5vh;

    }

    .homeOperations{
        width: 80%;
        height: 10%;
    }
        .homeApply{
            display: flex;
            align-items: center;
            justify-content: center;
            color: aquamarine;
            float: right;
            width: 15%;
            height: 100%;
            border-radius: 2vh;
            border: 2px solid darkcyan;
        }

@media (pointer:none), (pointer:coarse) {
    .homeLogo{
        display: none;
        height: 30%;
        align-self: center;
        justify-self: center;
        color: #61dafb;
        font-size: 6vh;
        font-family: 'Merienda', cursive;
    }

    .homeText{
        color: white;
        font-size: 3vh;

    }

}


