.AboutsUs{
    width: 100%;
    height: 100%;
    padding-top: 5%;
    background: -webkit-linear-gradient(silver, lightcyan);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.aboutUsLeftPane{
    justify-self: flex-start;
    margin-right: 10%;
    width: 45%;
    height: 80%;
}

.aboutUsRightPane{
    margin-left: 0%;
    margin-right: 5%;
    width: 40%;
    height: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

    .aboutUsTitle{
        align-self: flex-start;
        font-family: 'Merienda', cursive;
        height: 10%;
        background: -webkit-linear-gradient(white, blanchedalmond);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 4vh;
    }

    .aboutUsText{
        height: 70%;
        width: 100%;
        font-size: 3vh;
        //font-family: 'Arvo', serif;
        font-family: 'Zilla Slab', serif;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        align-content: flex-start;
        text-align: left;
        justify-content: flex-start;
    }

    .aboutUsText p{
        text-indent: 1.5vh;
    }

    #aboutUsTag{
        align-self: center;
    }
    .aboutUsStatsHolder{
        width: 100%;
        height: 20%;
        display: flex;
        flex-direction: row;
        align-items: center;
        align-content: center;
        justify-content: space-around;
    }

    .statNumber{
        background: -webkit-linear-gradient( var(--primary), black);
        -webkit-background-clip: text;
        font-size: 4vh;
        -webkit-text-fill-color: transparent;
    }


@media (pointer:none), (pointer:coarse) {
    .AboutsUs{
        padding-top: 10%;
    }
    .aboutUsLeftPane{
        justify-self: flex-start;
        margin-right: 10%;
        width: 0%;
        height: 80%;
    }

    .aboutUsRightPane{
        width: 90%;
        height: 100%;
    }
        .aboutUsTitle{
            align-self: flex-start;
            font-family: 'Merienda', cursive;
            height: 10%;
            background: -webkit-linear-gradient(white, blanchedalmond);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: 3vh;
        }
        .aboutUsText{
            height: 60%;
            width: 100%;
            font-size: 2.5vh;
            font-family: 'Zilla Slab', serif;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            align-content: flex-start;
            text-align: left;
            justify-content: flex-start;
        }
        .aboutUsStatsHolder{
            width: 100%;
            height: 30%;
            display: flex;
            flex-direction: row;
            align-items: center;
            align-content: center;
            justify-content: space-around;
        }
}