.BurgerMenu{
    display: none;
    width: 12%;
    height: 100%;
    float: right;
    margin-right: 5%;
    padding: 2%;
}

.NavBar{
    float: right;
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
    margin-right: 2%;
}

.navLi{
    color: white;
    text-align: center;
    list-style: none;
    width: 25%;
}

a{
    color: white;
    text-decoration: none;
    font-size: 2.5vh;
    font-family: 'Acme', sans-serif;
}
a:hover{
    color: grey;
}

.navApply{
    border: 2px solid var(--primary);
    padding: 1vh;
    border-radius: 2vh;
}

@media (pointer:none), (pointer:coarse) {
    .BurgerMenu{
        display: block;
    }
    .NavBar{
        position: fixed;
        top: 10%;
        right: 0;
        background-color: cadetblue;
        display: none;
        flex-direction: row;
        align-items: center;
        align-content: center;
        margin-right: 0%;
        list-style-type: none;
    }
    .navLi{
        display: block;
        margin-bottom: 10%;
        color: white;
        text-align: center;
        list-style: none;
        width: 100%;




    }

    Link{
        display: block;
        color: white;
        text-decoration: none;
        font-size: 4vh;
        font-family: 'Acme', sans-serif;
    }
    a:hover{
        color: grey;
    }
}